'use strict';

var alt = require('../alt');
var testUtils = require('../utils/testUtils.js');

var testActions = alt.createActions({

    setIndexSelected(index) {
        this.dispatch(index)
    },

    postSearch()
    {
       var _this = this;
       console.log('postSearch() function called....');
       testUtils.send()
       .then (function(response)
       {
           if (response.status === 200)
           {
               return response.json().then(function(indexData){
                   console.log("index data: ", indexData);
                   _this.dispatch(indexData);
               });
           }
           else
           {
               console.log('data empty!');
               return null;
           }
       })
       .then(function()
       {
            _this.actions.setIndexSelected(indexData);
           // _this.actions.updateCurrentUser(user || {});
           // _this.actions.updateLoggedIn(user ? true : false);
       })
    }

});

module.exports = testActions;

